ios from 'axios';\n\n  let data = {\n    imageSources: [],\n    workName: null,\n  };\n\n  export default {\n    name: 'work-view',\n    data () {\n      return data;\n    },\n    methods: {\n      getWorkName (name) {\n        window.scrollTo(0, 0);\n        return name.split('.')[0];\n      }\n    },\n    computed: {\n      getThumbNails () {\n        if (data.imageSources.length !== 0) return;\n        axios.get('/thumb')\n          .then(function (response) {\n            response.data.forEach(d => {\n              let obj = {\n                url: window.location.href + 'thumb/' + d,\n                name: d.split('_')[1],\n              }\n              data.imageSources.push(obj);\n            });\n          });\n      },\n    },\n  }\n</script>\n\n<style>\n  .my-work {\n    width: 100%;\n    margin-bottom: 5%;\n  }\n  .my-work img {\n    width: 100%;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\n  }\n  .thumbnail-container {\n    position: relative;\n    display: inline-block;\n    width: 50%;\n    transition: all ease 0.8s;\n  }\n  .view-work-btn {\n    display: inline-block;\n    width: 100%;\n    height: 100%;\n    padding-top: 25%;\n    text-align: center;\n    color: #FFFFFF;\n    font-size: 1.0rem;\n    font-weight: 600;\n    z-index: 1;\n  }\n  .thumbnail-container .overlay {\n    visibility: hidden;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(95, 190, 207, 0.4);\n    z-index: 0;\n  }\n  .thumbnail-container:hover .overlay {\n    visibility: visible;\n  }\n</style>\n"],"sourceRoot":""}]);

